@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: "Poppins", sans-serif;
    margin: 0;
    padding: 0;
    font-size: 18px;
    overflow-x: hidden;
}

* {
    box-sizing: border-box;
}

:root {
    --cyan: hsl(180, 66%, 49%);
    --dark-violet: hsl(257, 27%, 26%);
    --red: hsl(0, 87%, 67%);
    --gray: hsl(0, 0%, 75%);
    --grayish-violet: hsl(257, 7%, 63%);
    --very-dark-blue: hsl(255, 11%, 22%);
    --very-dark-violet: hsl(260, 8%, 14%);
}

.container {
    max-width: 1100px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

ul li {
    list-style-type: none;
}

img {
    width: 100%;
    max-width: 100%;
}

.btn {
    border: none;
    cursor: pointer;
    background-color: hsl(180, 66%, 49%);
    background-color: var(--cyan);
    padding: 8px 1.5rem;
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    border-radius: 1rem;
    transition: 0.4s ease-in;
}

.btn:focus {
    outline: none;
}

.btn:hover {
    -webkit-filter: brightness(130%);
            filter: brightness(130%);
}

@media screen and (max-width: 1150px) {
    .container {
        max-width: 900px;
    }
}

@media screen and (max-width: 950px) {
    .container {
        max-width: 800px;
    }
}

@media screen and (max-width: 850px) {
    .container {
        max-width: 768px;
    }
}

@media screen and (max-width: 800px) {
    .container {
        max-width: 100%;
        padding: 0 2rem;
    }
}
.boost {
    text-align: center;
    background: var(--dark-violet) url(/url-shortening-app/static/media/bg-boost-desktop.772154c9.svg) no-repeat center;
    overflow: hidden;
    color: #fff;
    padding: 3rem 1rem;
}

.boost .btn,
h2 {
    margin: 10px;
}

@media screen and (max-width: 800px) {
    .boost {
        background: var(--dark-violet) url(/url-shortening-app/static/media/bg-boost-mobile.4b04bd89.svg) no-repeat center;
        background-size: cover;
    }
}
footer {
    background: var(--very-dark-violet);
    padding: 3rem;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.footer-logo {
    cursor: pointer;
    -webkit-filter: invert(100%);
            filter: invert(100%);
}

.footer-links {
    display: flex;
    justify-content: space-between;
}

.footer-links div {
    padding: 10px 2rem;
}

.footer-links div p {
    color: #fff;
    margin-bottom: 14px;
}

.footer-links div ul {
    margin: 0;
    padding: 0;
}

.footer-links div ul li {
    font-size: 14px;
    font-weight: 500;
    padding: 8px 0;
}

.footer-links div ul li a {
    color: var(--gray);
    transition: 0.4s ease-in;
}

.footer-links div ul li a:hover {
    color: var(--cyan);
}

.social {
    display: flex;
    justify-content: space-between;
}

.social div {
    padding: 10px;
}

.social div img {
    cursor: pointer;
    transition: 0.4s ease-in;
}

.social div img:hover {
    -webkit-filter: invert(50%) hue-rotate(175deg);
            filter: invert(50%) hue-rotate(175deg);
}

@media screen and (max-width: 800px) {
    .footer-container,
    .footer-links {
        flex-direction: column;
        flex-wrap: wrap;
        text-align: center;
    }
    .footer-logo {
        width: 120px;
        margin: 0 auto;
    }
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    font-size: 0.9rem;
    padding: 10px 0;
    position: relative;
}

.right-header {
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    width: 90%;
}

.left-header div {
    cursor: pointer;
}

nav ul li {
    display: inline-block;
    padding: 0 1rem;
}

nav a {
    font-weight: 700;
    color: var(--gray);
    transition: 0.4s ease-in;
}

.buttons a {
    padding: 0 1rem;
    color: var(--gray);
    transition: 0.4s ease-in;
    font-weight: 700;
}

nav a:hover {
    color: var(--very-dark-blue);
}

.buttons a:hover {
    color: var(--very-dark-blue);
}

.bar1,
.bar2,
.bar3 {
    width: 30px;
    height: 5px;
    background-color: var(--gray);
    margin: 6px 0;
    transition: 0.4s;
}

.hamburger {
    display: none;
    cursor: pointer;
}

.change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
    opacity: 0;
}

.change .bar3 {
    transform: rotate(45deg) translate(-8px, -8px);
}

@media screen and (max-width: 800px) {
    .hamburger {
        display: block;
    }
    .visible {
        display: block;
    }
    .hidden {
        display: none;
    }
    .left-header div {
        width: 100px;
    }
    header {
        padding: 1rem !important;
    }
    .right-header {
        margin: 0 auto;
        position: absolute;
        padding: 0.5rem 1.5rem 2rem 1.5rem;
        text-align: center;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 999;
        flex-direction: column;
        flex-wrap: wrap;
        border-radius: 15px;
        background: var(--dark-violet);
    }
    nav ul li {
        display: block;
        padding: 1rem 0;
        font-size: 1rem;
    }
    nav ul {
        text-align: center;
        padding: 0 !important;
    }
    nav a {
        color: #fff;
    }
    .buttons a {
        padding: 10px 0;
    }
    .buttons a,
    .buttons .btn {
        color: #fff;
        font-size: 1rem;
        display: block !important;
        width: 100% !important;
    }
    .buttons a:hover,
    nav a:hover {
        color: var(--gray);
    }
    .menu-line {
        border-top: 1px solid var(--gray);
        width: 100%;
    }
}
.headerInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5rem;
}

.img {
    position: relative;
    flex-shrink: 1;
    left: 10%;
}

.info {
    flex-shrink: 2;
}

.info p {
    margin-bottom: 1rem;
}

h1 {
    font-size: 4rem;
    color: var(--very-dark-blue);
    margin: 0;
}

p {
    font-size: 14px;
    color: var(--gray);
}

@media screen and (max-width: 950px) {
    h1 {
        font-size: 2.5rem;
    }
    p {
        font-size: 12px;
    }
    .info {
        flex-shrink: 1;
    }
}

@media screen and (max-width: 800px) {
    .headerInfo {
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }
    .info {
        text-align: center;
    }
    .img {
        left: 0;
    }
}
form {
    background: url(/url-shortening-app/static/media/bg-shorten-desktop.0d9742f4.svg) no-repeat var(--dark-violet);
    padding: 2.7rem 1.7rem;
    border-radius: 12px;
}

.shorten-container {
    position: relative;
    top: -4rem;
}

.error-msg {
    position: absolute;
    bottom: 10px;
    left: 1.7rem;
    padding: 0;
    margin-top: 5px;
    font-style: italic;
    color: var(--red);
    font-weight: 500;
    font-size: 13px;
}

.true {
    display: block;
}

.false {
    display: none;
}

input {
    border: none;
    width: 80%;
    padding: 1rem;
    border: 2px solid #fff;
    border-radius: 10px;
    font-family: "Poppins", sans-serif;
}

input:focus {
    outline: none;
}

form .btn {
    border-radius: 6px !important;
    padding: 0.9rem 1.7rem;
    width: 18%;
    margin-left: 1rem;
}

.isEmpty {
    border: 2px solid var(--red);
    color: var(--red);
}

.isEmpty::-webkit-input-placeholder {
    color: var(--red);
}

.isEmpty:-ms-input-placeholder {
    color: var(--red);
}

.isEmpty::placeholder {
    color: var(--red);
}

@media screen and (max-width: 950px) {
    form .btn {
        width: 17%;
    }
}

@media screen and (max-width: 850px) {
    form .btn {
        padding: 0.9rem 1rem;
    }
}

@media screen and (max-width: 800px) {
    input {
        width: 100%;
    }
    form {
        padding: 1rem;
    }
    form .btn {
        width: 100%;
        margin: 10px 0;
    }
    form {
        background: url(/url-shortening-app/static/media/bg-shorten-mobile.41c34b33.svg) no-repeat top right var(--dark-violet);
    }
}
.card {
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    background: #fff;
    border-radius: 10px;
    padding: 10px 1rem;
    margin: 10px 0;
}

.left {
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
}

.orginal {
    color: var(--very-dark-violet);
}

.short {
    margin-right: 1rem;
    color: var(--cyan);
}

.left .btn {
    border-radius: 4px;
}

@media screen and (max-width: 800px) {
    .card,
    .left {
        flex-direction: column;
        flex-wrap: wrap;
        align-items: flex-start;
        width: 100%;
    }
    .short {
        margin: 10px 0;
    }
    .orginal {
        width: 100%;
        overflow: hidden;
    }
    .left .btn {
        width: 100%;
    }
}
.statistics {
    background: rgb(239, 241, 247);
    padding-bottom: 5rem;
    position: relative;
}

.stat-header {
    text-align: center;
    width: 360px;
    margin: 2rem auto 6rem auto;
}

.stat-header h1 {
    font-size: 1.8rem;
}

.body-card-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}

.body-card {
    background: #fff;
    border-radius: 10px;
    padding: 1rem;
    position: relative;
}

.body-img {
    width: 70px;
    height: 70px;
    position: absolute;
    top: -40px;
    padding: 13px;
    border-radius: 50%;
    background-color: var(--dark-violet);
}

.body-card h1,
p {
    margin: 0;
}

.top {
    top: -3rem;
}

.bottom {
    bottom: -3rem;
}

.line {
    border: 3px solid var(--cyan);
    background: var(--cyan);
    width: 100%;
    position: relative;
    bottom: -6rem;
}

@media screen and (max-width: 800px) {
    .body-card-container {
        grid-template-columns: repeat(1, 1fr);
        text-align: center;
    }
    .line {
        transform: rotate(270deg);
        bottom: -15rem;
        width: 70%;
    }
    .body-img {
        left: 50%;
        transform: translateX(-50%);
    }
    .stat-header {
        width: 350px;
    }
}
